@font-face {
  font-family: DINNext;
  src: url("../src/helpers/fonts/DINNext/DINNextLTPro-Heavy.otf");
}

.App {
  width: 2000px;
  height: 1050px;

  margin-left: auto;
  margin-right: auto;

  position: relative;
  background-image: url("./img/Desktop_cropped1050.jpg");
  overflow: hidden;
}

.openModalButton {
  position: absolute;
  top: 50%;
  left: 43%;

  padding: 10px;

  font-size: 50px;
  font-family: DINNext;

  background-color: white;
  color: black;
  border-radius: 12px;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.cup {
  position: absolute;
  top: 0%;
  left: 30%;
  background-image: url(".//img/cup.png");
  width: 100%;
  height: 999px;
  background-repeat: no-repeat;
  transform: translate(0%, 110%);
  animation: cupAndSparksAnimation 4s;
}

.cupbackground {
  position: absolute;
  top: 0%;
  left: 0%;
  background-image: url(".//img/cupShadow.png");
  width: 100%;
  height: 1050px;
  background-repeat: no-repeat;
  opacity: 0;
  animation: cupbackgroundAnimation 4s;
}

@keyframes cupbackgroundAnimation {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

.sparks1 {
  position: absolute;
  width: 100%;
  height: 2000px;
  top: 1px;
  left: 1px;
  background-image: url("./img/sparks.png");
  transform: translate(0%, 100%);
  animation: cupAndSparksAnimation 4s;
  animation-delay: 0.2s;
}

@keyframes cupAndSparksAnimation {
  0% {
    transform: translate(0%, 110%);
  }
  50% {
    transform: translate(0%, 0%);
  }
  100% {
    transform: translate(0%, 0%);
  }
}

.bg1 {
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(0%, 0%);
  background-image: url("./img/bg1.png");
  width: 2000px;
  height: 1050px;
  background-size: contain;
  background-repeat: no-repeat;
  transform: translate(-100%, 0%);
  animation: bg1Animation 8s;
  animation-delay: 0s;
}

@keyframes bg1Animation {
  0% {
    transform: translate(-100%, 0%);
  }
  10% {
    transform: translate(-100%, 0%);
  }
  35% {
    transform: translate(0%, 0%);
  }
  75% {
    transform: translate(0%, 0%);
  }
  100% {
    transform: translate(-100%, 0%);
  }
}

.bg2 {
  position: absolute;
  top: 0;
  left: 0;
  background-image: url("./img/bg2.png");
  width: 2000px;
  height: 1050px;
  background-size: contain;
  background-repeat: no-repeat;
  margin-left: 345px;
  transform: translate(0%, 100%);
  animation: bg2Animation 8s;
  animation-delay: 0s;
}

@keyframes bg2Animation {
  0% {
    transform: translate(100%, 0%);
  }
  10% {
    transform: translate(100%, 0%);
  }
  35% {
    transform: translate(0%, 0%);
  }
  75% {
    transform: translate(0%, 0%);
  }
  100% {
    transform: translate(100%, 0%);
  }
}

.text {
  position: absolute;
  top: 20%;
  left: 35%;
  font-family: DINNext;
  font-size: 300px;
  color: #fff;
  text-shadow: 10px 5px 32px rgba(0, 0, 0, 1);
  opacity: 0;
  transform: scale(0, 0);
  animation: textAnimation 3s;
  animation-delay: 2.5s;
}
.text2 {
  position: absolute;
  top: 44%;
  left: 35%;
  font-family: DINNext;
  font-size: 210px;
  color: #fff;
  text-shadow: 10px 5px 32px rgba(0, 0, 0, 1);
  transform: translate(0%, -300%);
  animation: textAnimation 3s;
  animation-delay: 2.6s;
}

.text3 {
  position: absolute;
  top: 62%;
  left: 35%;
  font-family: DINNext;
  font-size: 122px;
  color: #fff;
  text-shadow: 10px 5px 32px rgba(0, 0, 0, 1);
  transform: scale(0, 0);
  animation: textAnimation 3s;
  animation-delay: 2.7s;
}

@keyframes textAnimation {
  0% {
    opacity: 0.5;
    transform: scale(0, 0);
  }
  25% {
    opacity: 1;
    transform: scale(1, 1);
  }
  50% {
    opacity: 1;
    transform: scale(1, 1);
  }
  75% {
    opacity: 1;
    transform: scale(1, 1);
  }
  100% {
    opacity: 0.5;
    transform: scale(0, 0);
  }
}

.logo1 {
  position: absolute;
  top: 30%;
  left: 10%;
  transform: translate(0%, 0%);
  background-image: url("./img/logo1.png");
  width: 400px;
  height: 400px;
  background-size: contain;
  background-repeat: no-repeat;
  transform: scale(0, 0);
  animation: Logo1Animation 5s;
  animation-delay: 3s;
}

@keyframes Logo1Animation {
  0% {
    opacity: 0.5;
    transform: scale(0, 0);
  }
  25% {
    opacity: 1;
    transform: scale(1, 1);
  }
  60% {
    opacity: 1;
    transform: scale(1, 1);
  }
  100% {
    opacity: 1;
    transform: translate(-200%, 0);
  }
}

.logo2 {
  position: absolute;
  top: 30%;
  right: 10%;
  transform: translate(0%, 0%);
  background-image: url("./img/logo2.png");
  width: 400px;
  height: 400px;
  background-size: contain;
  background-repeat: no-repeat;
  transform: scale(0, 0);
  animation: Logo2Animation 5s;
  animation-delay: 3s;
}

@keyframes Logo2Animation {
  0% {
    opacity: 0.5;
    transform: scale(0, 0);
  }
  25% {
    opacity: 1;
    transform: scale(1, 1);
  }
  60% {
    opacity: 1;
    transform: scale(1, 1);
  }
  100% {
    opacity: 1;
    transform: translate(200%, 0);
  }
}

.banner {
  position: absolute;
  top: 0%;
  right: 0%;

  width: 100%;
  height: 1050px;

  background-image: url("./img/banner.png");
  overflow: hidden;

  transform: scale(0, 0);
  animation: bannerAnimation 8s;
  animation-delay: 3s;
  animation-fill-mode: forwards;
}

@keyframes bannerAnimation {
  0% {
    transform: scale(0, 0);
  }
  25% {
    transform: scale(1, 1);
  }
  60% {
    transform: scale(1, 1);
  }
  100% {
    opacity: 1;
    transform: scale(1, 1);
  }
}

.bannerBackground {
  position: absolute;
  top: 0%;
  left: 0%;
  background-image: url(".//img/cupShadow.png");
  width: 100%;
  height: 1050px;
  background-repeat: no-repeat;
  opacity: 0;
  animation: bannerBackgroundAnimation 4s;
  animation-delay: 4.5s;
}

@keyframes bannerBackgroundAnimation {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 0;
  }
}

.closeButton {
  position: absolute;
  top: 1%;
  right: 1%;

  border: none;
  background-color: transparent;

  cursor: pointer;

  width: 51px;
  height: 51px;

  background-image: url("./img/plusSign.png");
  background-size: contain;
}

.spotButton {
  position: absolute;
  top: 10%;
  right: 17%;

  border: none;
  background-color: transparent;

  cursor: pointer;

  width: 109px;
  height: 109px;

  transform: rotate(45deg);

  background-image: url("./img/plusSign.png");
  background-size: contain;

  opacity: 0;
  animation: spotButtonAnimation 8s;
  animation-delay: 3s;
  animation-fill-mode: forwards;
}

@keyframes spotButtonAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    transform: rotate(45deg);
  }
}

.spotText {
  position: absolute;
  top: 21%;
  right: 14%;
  font-family: DINNext;
  font-size: 30px;
  text-transform: uppercase;
  color: #fff;
  text-shadow: 10px 5px 32px rgba(0, 0, 0, 1);
  cursor: pointer;

  transform: scale(0, 0);
  animation: bannerAnimation 8s;
  animation-delay: 3s;
  animation-fill-mode: forwards;
}

.modalBackground {
  position: absolute;
  top: 0;
  left: 0;
  background-image: url(".//img/videoBackground.png");
  background-repeat: no-repeat;
  width: 2000px;
  height: 1050px;
  animation: modalBackgroundAnimation 0.5s;
}

@keyframes modalBackgroundAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.backgroundVideo {
  position: absolute;
  top: 15%;
  left: 17%;
  width: 1280px;
  height: 720px;
}

.closeVideoButton {
  position: absolute;
  top: 10%;
  right: 17%;

  border: none;
  background-color: transparent;

  cursor: pointer;

  width: 109px;
  height: 109px;

  background-image: url("./img/plusSign.png");
  background-size: contain;
}
